// #73322F
// #F1B0AC
body{
  font-family: 'Barlow', sans-serif;
  font-size: 18px;
  color: #73322F;
  margin: 0;
  overflow-x: hidden;
  max-width: 1440px;
  @media (min-width:1440px) {
    margin-left: auto;
    margin-right: auto;
  }
}
a{
  text-decoration: none;
  color: #73322F;
  font-family: 'Barlow', sans-serif;
  font-size: 18px;
}
ul{
  list-style: none;
  padding: 0;
}
img{
  width: 100%;
}
.section{
  margin: 0 20px;
  @media (min-width: 800px) {
    margin: 0 40px;
  }
  &--header{
    margin-top: 20px;
    margin-bottom: 20px;
    position: relative;
  }
  &--intro{
    @media (min-width:1440px) {
      display: flex;
      margin: 0 40px;
    }
  }
  &--stepper{
    margin-bottom: 150px;
    @media (min-width:1100px) {
      display: flex;
      flex-wrap: wrap;
    }
    @media (min-width:1440px) {
      justify-content: center;
      margin-top: 200px;
    }
  }
  &--avantages{
    margin-bottom: 150px;
    @media (min-width:1440px) {
      margin-top: 300px;
    }
  }
  &--cta{
    margin-bottom: 150px;
    @media (min-width:1440px) {
      margin-top: 300px;
    }
  }
  &--footer{
    margin-bottom: 50px;
  }
  &--app{
    background-color: #F1B0AC;
    padding: 40px 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    margin: 0;
    text-align: center;
  }
  &--lists{
    width: auto;
  }
  &--legende{
    display: flex;
    flex-direction: column;
    margin-bottom: 50px;
  }
  &--map{
    margin-bottom: 50px;
  }
  &--price{
    margin-bottom: 50px;
  }
}
.test{
  display: flex;
  justify-content: space-around;
}
.logo{
  font-family: 'Cormorant', serif;
  font-size: 30px;
  text-transform:uppercase;
  font-weight: 500;
}
.title{
  font-family: 'Cormorant', serif;
  font-size: 40px;
  color: #73322F;
  font-weight: 500;
  max-width: 500px;
  @media (min-width:1100px) {
    max-width: 700px;
  }
  &--big{
    font-size: 50px;
    @media (min-width:1100px) {
      margin-bottom: 0;
      font-size: 70px;
    }
    @media (min-width:1440px) {
      margin-right: 50px;
    }
  }
  &--weight{
    font-weight: 700;
  }
  &--small{
    font-weight: 700;
    margin-bottom: 10px;
  }
  &--avantages{
    color: white;
    text-align: center;
    font-size: 50px;
    font-weight: 700;
    z-index: 10;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
    @media (min-width:1100px) {
      font-size: 70px;
    }
  }
  &--cta{
    margin-left: auto;
    margin-right: auto;
  }
  &--data{
    font-family: 'Barlow', sans-serif;
    font-size: 20px;
    font-weight: 300;
  }
  &--h3{
    font-size: 30px;
  }
  &--marg{
    margin: 0px 10px;
  }
}
.text{
  font-size: 20px;
  font-weight: 300;
  line-height: 1.4;
  max-width: 50ch;
  @media (min-width:1100px) {
    font-size: 24px;
  }
  &--step{
    margin-bottom: 70px;
  }
  &--intro{
    @media (min-width:1440px) {
      margin-right: 100px;
    }
  }
}
.link--cta:hover::after{
    width: 100%;
    right: 0;
}
.link{
  &--cta{
    font-family: 'Cormorant', serif;
    font-size: 25px;
    font-weight: 700;
    padding: 10px 20px;
    border: 2px solid #73322F;
    margin-top: 30px;
    display : inline-block;
    transition: all .3s ease-in-out;
    z-index: 20;
    position: relative;
    &:hover{
      color: white;
    }
    &::after{
      content: '';
      display: block;
      width: 0%;
      height: 100%;
      background-color: #73322F;
      position: absolute;
      top: 0;
      left: 0;
      transition: all .3s ease-in-out;
      z-index: -1;
    }
  }
  &--dwm{
    margin-top: 20px;
    display: block;
    height: 30px;
    width: 100px;
    background-image: url(../images/logo_dwm.svg);
    background-repeat: no-repeat;
    background-size: contain;
  }
}
label{
  color: #73322F;
  border: 1px solid #73322F;
  padding: 10px 20px;
  display: inline-block; 
  margin: 10px 10px;
  transition: all .3s ease-in-out;
  cursor: pointer;
}
input{
  display:none;

  &:checked + label{
    background-color: #73322F;
    color: #fff;
  }
}
.img{
  &--map{
    width: 50%;
    margin-left: auto;
    margin-top: -50px;
    margin-right: 20px;
    display: block;
    @media (min-width:600px) {
      margin-top: -100px;
    }
    @media (min-width:900px) {
      width: 40%;
    }
    @media (min-width:1440px) {
      position: absolute;
      left: -130px;
      bottom: -40px;
      z-index: 10;
      width: auto;
      height: 500px;
      margin: 0;
    }
  }
  &--step{
    height: 250px;
    object-fit: cover;
  }
}
.carte{
  width: 100%;
  height: auto;
}
.container{
  &--intro{
    margin: 0 20px;
    @media (min-width: 800px) {
      margin: 0 40px;
    }
    @media (min-width:1440px) {
      margin: 0;
      height: 760px;
      width: 50%;
    }
  }
  &--cocktail{
    margin-top: 50px;
    @media (min-width:1440px) {
      margin: 0;
      position: absolute;
      height: 760px;
      width: 50%;
      top: 0;
      right: 0;
    }
  }
  &--map{
    width: 100%;
    height: 300px;
    display: block;
    background-color:#F1B0AC;
    margin-top: -200px;
    background-image: url(../images/map.png);
    background-repeat: none;
    background-size: cover;
    margin-bottom: 100px;
    @media (min-width:600px) {
      height: 400px;
      margin-top: -350px;
    }
    @media (min-width:900px) {
      margin-top: -400px;
    }
    @media (min-width:1100px) {
      height: 500px;
      margin-top: -500px;
    }
    @media (min-width:1440px) {
      margin:0;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  &--step{
    max-width: 400px;
    margin: 0 auto 80px auto;
    border: 2px solid #C4B6B5;
  }
  &--avantages{
    width: 100%;
    height: 300px;
    background-image: url(../images/avant.jpg);
    background-repeat: none;
    background-size: cover;
    position: relative;
    @media (min-width:1100px) {
      height: 500px;
    }
    &::after{
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba($color: #000000, $alpha: 0.3);
      z-index: 0;
    }
  }
  &--listavantages{
    margin: 0 20px;
    text-align: center;
    @media (min-width:1100px) {
      display: flex;
      flex-wrap: wrap;
      margin-top: 150px;
    }
  }
  &--argu{
    max-width: 450px;
    display: block;
    margin: 70px auto;
    @media (min-width:1100px) {
      margin: 50px auto;
    }
    & h2{
      margin-left: auto;
      margin-right: auto;
    }
  }
  &--cta{
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
    @media (min-width:900px) {
      display: flex;
      max-width: 800px;
    }
  }
  &--imgcta{
    height: 400px;
    @media (min-width:900px) {
      height: 350px;
      width: 50%;
    }
    & img{
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  &--footer{
    @media (min-width:600px) {
      display: flex;
      justify-content: space-between;
      align-items: baseline;
    }
  }
}
.bloc{
  &--step{
    text-align: center;
    padding: 20px;
    position: relative;
    @media (min-width:1100px) {
      height: 260px;
    }
  }
  &--cta{
    background-color: #F1B0AC;
    padding: 30px 20px;
    text-align: center;
    @media (min-width:600px) {
      padding: 50px 20px;
    }
    & h2{
      margin-top: 0;
      margin-bottom: 0;
      @media (min-width:1100px) {
        max-width: 500px;
      }
    }
    & a{
      margin-top: 30px;
    }
  }
  &--link{
    margin-top: 50px;
    @media (min-width:600px) {
      margin-top: 0;
    }
  }
}
.step{
  display: block;
  width: 100px;
  height: 100px;
  position: absolute;
  left: 50%;
  bottom: -35px;
  transform: translateX(-50%);
  & img{
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
}
.list{
  &--footer{
    @media (min-width:600px) {
      margin-top: 0;
    }
  }
}
.footer{
  &__el{
    margin: 10px 0;
    @media (min-width:600px) {
      &:nth-child(1){
        margin-top: 0;
      }
    }
  }
}
.footer__el a{
  transition: all .3s ease-in-out;
}
.footer__el a:hover{
  border-bottom: 1px solid #73322F;
}
.solid{
  border-bottom: 15px solid #F6CBCB;
  padding: 0px 16px;
  border-radius: 5px;

  &--second{
    border-bottom: 15px solid #DF908B;
  }
  &--three{
    border-bottom: 15px solid #570F0F;
  }
}


